<template>
    <div>
        <div class="message-alert">
            <div class="alert  alert-dismissible" :class="'alert-' + item.status" v-for="(item, index) in messages" :key="index" role="alert">
                {{ item.message }}
                <button type="button" class="close" @click="removeMessage(index)" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Alert',
    data() {
        return {
            messages: [],
        }
    },
    methods: {
        updateMessage(message, status) {
            const timestamp = Math.floor(new Date() / 1000)
            this.messages.push({
                message,
                status,
                timestamp,
            })
            this.removeMessageWithTiming(timestamp)
        },
        removeMessage(index) {
            this.messages.splice(index, 1)
        },
        removeMessageWithTiming(timestamp) {
            const vm = this
            setTimeout(() => {
                vm.messages.forEach((item, i) => {
                    if (item.timestamp === timestamp) {
                        vm.messages.splice(i, 1)
                    }
                })
            }, 5000)
        }
    },
    created() {
        const vm = this
        vm.$bus.$on('message:push', (message, status = 'warning') => {
            vm.updateMessage(message, status)
        })
    },
    beforeDestroy() {
        // [銷毀監聽事件]
        // 最好在组件銷毀前，清除 specialEvent 所有監聽 
        // 如果要指定哪個監聽對應方法，就需要傳入第二個參數(對應$on設定的方法)
        this.$bus.$off('message:push')
    }
}
</script>

<style lang="scss" scoped>
.message-alert {
    position: fixed;
    max-width: 50%;
    top: 56px;
    right: 20px;
    z-index: 1100;
}
</style>



